import React from "react";
import PropTypes from "prop-types";
import { Panel, PanelHeader } from "@vkontakte/vkui";
import { BackButton } from "../../components/BackButton";
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import { ArrowIcon } from "../../icons/ArrowIcon";
import Input from "@vkontakte/vkui/dist/components/Input/Input";

import "./Questionnaire.css";

const questions = [
  {
    id: 1,
    text: "Вы курили  в течение последнего года?",
    options: [
      { text: "Постоянно", value: 2 },
      { text: "Редко", value: 1 },
      { text: "Нет", value: 0 }
    ],
    input: null
  },
  {
    id: 2,
    text: "Укажите пол",
    options: [{ text: "Мужской", value: 0 }, { text: "Женский", value: 1 }],
    input: null
  },
  {
    id: 3,
    text: "Сколько Вам лет?",
    options: null,
    input: {
      type: "number",
      placeholder: "Введите Ваш возраст",
      getValue: age => {
        if (age > 75) {
          return 3;
        } else if (age > 60) {
          return 2;
        } else if (age > 50) {
          return 1;
        } else if (age > 40) {
          return 0.2;
        } else {
          return 0;
        }
      }
    }
  },
  {
    id: 4,
    text: "Сколько Вы весите?",
    options: null,
    input: {
      type: "number",
      placeholder: "Введите Ваш вес в кг",
      getValue: () => {
        return 0;
      }
    }
  },
  {
    id: 5,
    text: "Какой у Вас рост?",
    options: null,
    input: {
      type: "number",
      placeholder: "Введите Ваш рост в см",
      getValue: (height, questionNumber) => {
        const weight = answers[questionNumber - 1].value;

        const BMI = weight / ((height / 100) * (height / 100));

        if (BMI > 40) {
          return 1.5;
        } else if (BMI > 35) {
          return 1;
        } else if (BMI > 30) {
          return 0.5;
        } else if (BMI > 25) {
          return 0.25;
        } else {
          return 0;
        }
      }
    }
  },
  {
    id: 6,
    text:
      "У вас диагностировали какие-либо сердечно-сосудистые заболевания (атеросклероз, аритмия, гипертония, сахарный диабет)?",
    options: [
      { text: "Да, несколько", value: 4 },
      { text: "Да, одно", value: 2 },
      { text: "Нет", value: 0 }
    ],
    input: null
  },
  {
    id: 7,
    text: "Вы занимаетесь физической активностью хотя бы 2,5 часа в неделю?",
    options: [{ text: "Да", value: 0 }, { text: "Нет", value: 1.5 }],
    input: null
  },
  {
    id: 8,
    text: "Вы съедаете 250 грамм овощей или фруктов в день?",
    options: [{ text: "Да", value: 0 }, { text: "Нет", value: 1.5 }],
    input: null
  },
  {
    id: 9,
    text: "Вы испытывали значительный стресс в течение последнего года?",
    options: [{ text: "Да", value: 1 }, { text: "Нет", value: 0 }],
    input: null
  }
];

const maxRiskValue = 15.5;

let answers = [];

export class Questionnaire extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      questions,
      questionNumber: 0,
      inputValue: "",
      percentage: 0,
      riskValue: 0,
      lastChosenOption: {
        questionNumber: -1,
        value: 0
      }
    };
  }

  getProperPercentage = value => {
    if (value > 100) {
      return 100;
    } else if (value < 0) {
      return 0;
    } else {
      return value;
    }
  };

  handleChange = event => {
    const { value, name } = event.target;

    this.setState({ [name]: value });
  };

  calculatePercentage = ({
    question,
    questionNumber,
    option = null,
    value
  }) => {
    let curRiskValue = this.state.riskValue;
    const lastChosenOption = this.state.lastChosenOption;

    const valueToAdd = option
      ? option.value
      : question.input.getValue(value, questionNumber);

    if (lastChosenOption.questionNumber === questionNumber) {
      curRiskValue -= lastChosenOption.value;
    }

    console.log(curRiskValue);
    console.log(valueToAdd);

    if (option) {
      curRiskValue = curRiskValue + valueToAdd;
    } else {
      curRiskValue = curRiskValue + valueToAdd;
    }

    this.setState({
      percentage: this.getProperPercentage(
        Math.floor((curRiskValue / maxRiskValue) * 100)
      ),
      riskValue: curRiskValue,
      lastChosenOption: {
        questionNumber,
        value: valueToAdd
      }
    });
  };

  render() {
    const { questions = [null], questionNumber = 0, percentage } = this.state;
    const currentQuestion = questions[questionNumber];
    const buttonTitle =
      questionNumber !== questions.length - 1 ? "Дальше" : "Закончить";
    const currentQuestionId = currentQuestion.id;

    const borderColor = `rgb(${percentage}%, ${100 - percentage}%, 65%)`;

    return (
      <Panel id={this.props.id}>
        <PanelHeader left={<BackButton onClick={this.props.go} to="home" />}>
          Старт
        </PanelHeader>
        <Div className="questionnaire">
          <Div className="questionnaire__diagram-wrapper">
            <Div
              className="questionnaire__diagram"
              style={{ borderColor }}
            >{`${percentage}%`}</Div>
          </Div>
          <Div className="questionnaire__question">{currentQuestion.text}</Div>
          {currentQuestion.options ? (
            <Div className="questionnaire__options">
              {currentQuestion.options.map((option, index) => {
                return (
                  <Div
                    className="questionnaire__option"
                    key={index}
                    onClick={e => {
                      answers[questionNumber] = {
                        id: currentQuestionId,
                        value: option
                      };

                      this.calculatePercentage({
                        option,
                        question: currentQuestion,
                        questionNumber
                      });

                      const elements = document.getElementsByClassName(
                        "questionnaire__option-active"
                      );

                      elements.length &&
                        Array.from(elements).forEach(element => {
                          element.classList.remove(
                            "questionnaire__option-active"
                          );
                        });

                      if (
                        !e.currentTarget.classList.contains(
                          "questionnaire__option-active"
                        )
                      ) {
                        e.currentTarget.classList.add(
                          "questionnaire__option-active"
                        );
                      }
                    }}
                  >
                    <span className="questionnaire__option-text">
                      {option.text}
                    </span>
                  </Div>
                );
              })}
            </Div>
          ) : (
            <Div className="questionnaire__input">
              <Input
                type="number"
                placeholder={currentQuestion.input.placeholder}
                value={this.state.inputValue}
                alignment="center"
                onChange={event => {
                  const value = event.currentTarget.value;

                  answers[questionNumber] = {
                    id: currentQuestionId,
                    value: value
                  };

                  this.calculatePercentage({
                    question: currentQuestion,
                    questionNumber,
                    value
                  });

                  this.setState({ inputValue: value });
                }}
              />
            </Div>
          )}

          <Div className="questionnaire__control">
            <Button
              size="xl"
              level="2"
              onClick={event => {
                if (!answers[questionNumber]) {
                  return;
                }

                this.setState({ inputValue: "" });

                const elements = document.getElementsByClassName(
                  "questionnaire__option-active"
                );

                elements.length &&
                  Array.from(elements).forEach(element => {
                    element.classList.remove("questionnaire__option-active");
                  });

                if (questionNumber !== questions.length - 1) {
                  this.setState({ questionNumber: questionNumber + 1 });
                } else {
                  answers = [];
                  this.props.go(event);
                }
              }}
              data-to="home"
              className="questionnaire__control-button"
            >
              <div style={{ marginBottom: "10px" }}>{buttonTitle}</div>
              <ArrowIcon />
            </Button>
          </Div>
        </Div>
      </Panel>
    );
  }
}

Questionnaire.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired
};
